@font-face {
  font-family: "Roobert";
  src: url("~twitch-core-ui-fonts/fonts/Roobert-Regular.woff2") format("woff2"), url("~twitch-core-ui-fonts/fonts/Roobert-Regular.woff") format("woff");
  font-weight: 400; }

@font-face {
  font-family: "Roobert";
  src: url("~twitch-core-ui-fonts/fonts/Roobert-SemiBold.woff2") format("woff2"), url("~twitch-core-ui-fonts/fonts/Roobert-SemiBold.woff") format("woff");
  font-weight: 600; }

@font-face {
  font-family: "Roobert";
  src: url("~twitch-core-ui-fonts/fonts/Roobert-Bold.woff2") format("woff2"), url("~twitch-core-ui-fonts/fonts/Roobert-Bold.woff") format("woff");
  font-weight: 700; }

html, body, #root {
  font-family: "Roobert", "Helvetica Neue", Helvetica, Arial, sans-serif;
  width: 100%;
  height: 100%; }

.container {
  max-width: 180rem;
  margin: 0 auto;
  position: relative; }

.number {
  display: block;
  width: 3.4rem;
  min-width: 3.4rem;
  height: 3.4rem; }

.puppers {
  color: #0E0E10;
  padding: 10rem 7rem 0 7rem;

  @media (min-width: 1200px) {
    padding: 12rem 12rem 0 12rem; } }

.program-section {
  margin-top: 0;

  @media (min-width: 1200px) {
    margin-top: 60rem; }


  &:before {
    content: "";
    position: absolute;
    top: 70.4rem;
    bottom: 0;
    left: -9999px;
    right: 0;
    border-left: 9999px solid var(--color-background-base);
    box-shadow: 9999px 0 0 var(--color-background-base); } }

.pup {
  width: 100%; }
